@font-face {
  font-family: 'Helvetica';
  src:local('HelveticaforTarget-Bold'), url('HelveticaforTarget-Bold.ttf') format('truetype');
}

html{
  background: linear-gradient(#e66465, #9198e5);
  height: 100vh;
  text-align: center;
  margin: 0;
  
}

body{
  display: flex;
}

.site-header
{
  font-family: 'Helvetica', sans-serif;
  font-size: calc(100vw*.1718);
  margin: 0px;

}

#searchbar
{
  width: 25vw;
  height: 5vh;
  border: none;
  border-radius: 15px 15px 0px 0px;
  border-color: white;
  text-align: center;
  font-size: 2vw;
  font-family: 'Poppins', sans-serif;
  border-bottom: 1px dashed black;
}

#searchbar_hidden{
  width: 25vw;
  height: 5vh;
  border: none;
  border-radius: 15px 15px 15px 15px;
  border-color: white;
  text-align: center;
  font-size: 2vw;
  font-family: 'Poppins', sans-serif;
}

#searchbar_hidden:focus{
  outline: none;
}
#searchbar:focus{
  outline: none;
}

.Card{
  width: 50vw;
  height: 52vh;
  border-color: black;
  border-radius: 25px;
  background-color: white;
  margin: 10px;
  display: inline-block;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  text-transform: capitalize;
}

.search_dropdown_parent{
  display: flex;
  justify-content: center;
}

.search_dropdown{
  display: flex;
  font-family: 'Roboto', sans-serif;
  background-color: white;
  width: 25vw;
  border-radius: 0px 0px 15px 15px;
  position: absolute;
  z-index: 1;
  border-bottom: 1px solid black;
  box-shadow: inset;
  justify-content: center;
}

ul{
  margin: 0;
  padding: 0;
  overflow: hidden;
  padding: 10px;
}

li{
  font-size: 1.25vw;
  list-style-type: none;

}

.Types
{
  display: flex;
  justify-content: center;

}

#poke_name
{
  margin: 5px;
  font-size: 3vw;
  font-family: 'Cabin', sans-serif;

}

#poke_type
{
  margin: 5px;
  font-size: 2vw;
  font-family: 'Poppins', sans-serif;
  padding: 5px;
  border: 1px dotted black;
  border-radius: 15px;
}

.poke_image
{
  width: auto;
  height: 65%;
  border-radius: 25px 25px 0px 0px;
}

.Poke_NonExist{
  font-family: 'Open Sans', sans-serif;}

@media (max-width: 768px) {
  .Card {
    width: 90%;
    margin: 5px;
  }
  #searchbar {
    margin-top: 25px;
    width: 50%;
    font-size: 4vw;
    justify-content: center;
  }

  .search_dropdown{
    width: 49%;
  }

  .searchResult{
    font-size: 2.8vw;
  }

  .poke_image
  {
    width: auto;
    height: 65%;
    border-radius: 25px 25px 0px 0px;
  
  }
  #poke_name {
    font-size: 8vw;
  }
  #poke_type {
    font-size: 5vw;
  }
}